<template>

  <section class="createshop">
    <div class="container-scroller">
      <div class="container-fluid page-body-wrapper full-page-wrapper">
        <div class="content-wrapper d-flex align-items-center auth">
          <div class="row w-100 flex-grow">
            <div class="col-xl-4 col-lg-6 mx-auto">
              <div class="auth-form-light text-right p-5">

                <h4>ساخت فروشگاه</h4>
                <h6 class="font-weight-light">برای ساخت فروشگاه اطلاعات فروشگاه را وارد نمایید</h6>
                <form class="pt-3">

                  <div class="form-group">
                    <input v-model="title" type="text" class="form-control form-control-lg text-start" id="exampleInputEmail1" placeholder="عنوان">
                  </div>

                  <div class="form-group">
                    <input v-model="subDomain" type="text" class="form-control form-control-lg text-start" id="exampleInputEmail11" placeholder="دامنه">
                  </div>

                  <div class="form-group">
                    <textarea v-model="desc" class="form-control form-control-lg text-start" rows="3" placeholder="توضیحات" ></textarea>
                  </div>

                  <div class="mt-3">
                    <div :disabled="!!loading" class="btn btn-block btn-gradient-primary btn-lg font-weight-medium auth-form-btn" v-on:click="createShopTap">
                      <span
                          v-if="loading"
                          class="spinner-border spinner-border-sm mb-1"
                          role="status"
                          aria-hidden="true"
                      ></span>
                      <div v-else>
                        <h4>ساخت فروشگاه</h4>
                      </div>
                    </div>
                  </div>

                  <div v-show="errored" class="alert alert-danger mt-2">
<!--                    <strong style="font-family: ubuntu-light;">خطا!</strong>-->
                    <span style="font-family: ubuntu-light;">{{errorMessage}}</span>
                  </div>

                  <div v-show="success" class="alert alert-success mt-2">
                    <span style="font-family: ubuntu-light;">فروشگاه شما راه اندازی شد</span>
                  </div>
<!--                  <div class="my-2 d-flex justify-content-between align-items-center">-->
<!--                    <div class="form-check">-->
<!--                      <label class="form-check-label text-muted">-->
<!--                        <input type="checkbox" class="form-check-input">-->
