                </form>
              </div>
            </div>
          </div>
      </div>
      <!-- content-wrapper ends -->
      </div>
      <!-- page-body-wrapper ends -->
    </div>
  </section>

</template>


<script>

import api from '../../../const/api'

export default {
  name: 'createshop',
  data() {
    return {
      title: null,
      subDomain: null,
      desc: null,
      loading: false,
      errored: false,
      errorMessage: "",
      success: false
    };
  },
  methods: {
    createShopTap: function () {

      if (!this.title) {
        this.errored = true
        this.errorMessage = "عنوان فروشگاه را وارد نمایید"
        setTimeout( () => {
          this.errored = false
        }, 2000 )
        return;
      }

      if (!this.subDomain) {
        this.errored = true
        this.errorMessage = "دامنه فروشگاه را وارد نمایید"
        setTimeout( () => {
          this.errored = false
        }, 2000 )
        return;
      }

      if (!this.desc) {
        this.errored = true
        this.errorMessage = "توضیحات فروشگاه را وارد نمایید"
        setTimeout( () => {
          this.errored = false
        }, 2000 )
        return;
      }

      this.loading = true;
      const body = { title: this.title, desc: this.desc, subDomain: this.subDomain };
      this.axios
          .put(api.baseURL + '/sellers/seller', body,{
            headers: {
              Authorization: "Bearer " + localStorage.token
            }
          })
          .then(response => {

            // eslint-disable-next-line no-console
            console.log(response);
            this.loading = false;
            if (response.status == 200) {

              this.success = true
              setTimeout( () => {
                this.success = false
                this.$router.push({path: '/'});
              }, 2000 )

            } else {
              this.errored = true
              this.errorMessage = response.data.message
            }

          })
          .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error);
            this.errored = true
            this.errorMessage = 'دوباره تلاش کنید'
          });

    }

  }
}
</script>
